import { createGlobalStyle } from 'styled-components';

export default createGlobalStyle`
    * {
        margin: 0;
        padding 0;
        box-sizing: border-box;
    }

    html, body, #root {
        height: 100%;
        background-color: #FCFCFC;
        display: flex;
        flex-direction: column;
        font-family: 'Roboto', sans-serif;
    }
    
    *, button, input {
        border: 0;
        outline: 0;
    }
`;